<template>
    <div class="container">
        <h2>searchBar 搜索框组件</h2>
        <h3>搜索框组件</h3>
        <Pcode :code="code">
        </Pcode>

        <h3>属性</h3>
        <PAttrTable :data="attrs"/>

        <h3>事件</h3>
        <PEventTable :data="events"/>
    </div>
</template>

<script>
import PAttrTable from '@/components/PAttrTable.vue'
import PEventTable from '@/components/PEventTable.vue'
import Pcode from '@/components/Pcode.vue'

export default {
    name: 'button',
    components: {
        PAttrTable, PEventTable, Pcode
    },
    data () {
        return {
            attrs: [
                {
                    param: 'className',
                    desc: '组件最外层className',
                    type: 'String,Array',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'placeholder',
                    desc: '搜索框里默认显示文案',
                    type: 'String',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'actionName',
                    desc: '右侧文案',
                    type: 'String',
                    optional: '-',
                    default: '搜索'
                },
                {
                    param: 'actionStyle',
                    desc: '右侧自定义样式',
                    type: 'String,Array',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'inputType',
                    desc: '输入框类型',
                    type: 'String',
                    optional: 'text,number,idcard,digit',
                    default: 'text'
                },
                {
                    param: 'value',
                    desc: '必填，输入框当前值，开发者需要通过 onChange 事件来更新 value 值',
                    type: 'String',
                    optional: '-',
                    default: '-'
                },
                {
                    param: 'fixed',
                    desc: '是否固定在头部',
                    type: 'Boolean',
                    optional: '-',
                    default: 'false'
                }
            ],
            events: [
                {
                    name: 'onChange',
                    desc: '输入框值改变时触发的事件，开发者需要通过 onChange 事件来更新 value 值变化，onChange 函数必填',
                    param: `value,event 对象`
                },
                {
                    name: 'onActionClick',
                    desc: '右侧点击触发事件',
                    param: `-`
                }
            ],
            code: `
<script>
    import Taro, { Component } from '@tarojs/taro'
    import { View } from '@tarojs/components'
    import {
        WiSearchBar,
    } from 'hosjoy-wi-ui'

    export default class Index extends Component {

        constructor(props){
            super(props)
            this.state = {
                value:''
            }
        }

        onChange(v){
            this.setState({
                value:v
            })
            return v
        }

        searchClick(){
            console.log('搜索按钮点击')
        }

          render () {
              return (
                  <View className='index'>
                      <WiSearchBar
                          onChange={this.onChange.bind(this)}
                          value={this.state.value}
                          onActionClick={this.searchClick.bind(this)}
                      />
                  </View>
              )
          }
}
<\/script>`
        }
    },
}
</script>

<style lang="scss" scoped>
.container {
    padding-bottom: 50px;
}
</style>
